.profilePage .profile-pic {
  height: 230px;
  width: 230px;
  background-size: contain;
  background-color: #ddd;
  background-position: center center;
  background-repeat: no-repeat;
  box-shadow: 5px 0px 7px #0000004d;
  position: relative; }
  .profilePage .profile-pic .fa-camera {
    cursor: pointer;
    background-color: #b4b4b4;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    border-radius: 50%;
    position: absolute;
    right: 10px;
    bottom: 10px; }

.profilePage .user-meta {
  padding: 15px;
  color: #fff;
  width: calc(100% - 230px); }
  .profilePage .user-meta .button {
    position: absolute;
    right: 25px;
    top: 10px; }
  .profilePage .user-meta .name {
    font-weight: bold; }
  .profilePage .user-meta .job, .profilePage .user-meta .location {
    font-size: 12px; }
  .profilePage .user-meta .hr-div {
    margin: 10px 0;
    padding: 10px 0;
    border-top: 1px solid #ffffff36;
    border-bottom: 1px solid #ffffff36; }
  .profilePage .user-meta .bio {
    font-size: 13px; }

.profilePage .create-post-customize {
  width: 100%;
  display: block; }
  .profilePage .create-post-customize ul {
    width: 100%; }

.profilePage .tabPanelItem {
  margin-top: 0;
  width: 100%; }

.profilePage #switch {
  padding: 0;
  list-style: none; }
  .profilePage #switch li {
    margin: 10px;
    padding: 5px 10px;
    border-radius: 15px;
    font-weight: bold;
    font-size: 14px;
    cursor: pointer; }
    .profilePage #switch li.active, .profilePage #switch li:hover {
      color: #5931ea;
      background-color: #eaeaea; }

.profilePage .add-new {
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: flex-start;
  color: #5931ea;
  font-weight: bold;
  font-size: 16px;
  line-height: 13px;
  margin-bottom: 15px; }
  .profilePage .add-new span {
    margin-left: 10px; }

.profilePage .sItem {
  border-bottom: 1px solid #ddd;
  padding: 10px; }
  .profilePage .sItem p {
    font-size: 16px;
    margin-bottom: 0; }
  .profilePage .sItem:last-child {
    border-bottom: 0; }

.profilePage .srow {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  border-bottom: 1px solid #ddd;
  padding: 10px; }
  .profilePage .srow label {
    width: 100px;
    font-size: 14px;
    margin: 0; }
    .profilePage .srow label.big {
      width: 50%; }
  .profilePage .srow .value {
    font-size: 14px;
    font-weight: bold;
    max-width: 400px;
    text-align: right;
    margin-left: 5px; }
  .profilePage .srow .pointer {
    color: #5931ea; }
  .profilePage .srow .left {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    width: 50%; }
    .profilePage .srow .left.col {
      flex-direction: column; }
    .profilePage .srow .left div {
      font-size: 14px;
      font-weight: bold; }
    .profilePage .srow .left span {
      display: block;
      font-size: 10px;
      color: #666; }
    .profilePage .srow .left .fa {
      margin-right: 10px;
      font-size: 20px;
      color: #222;
      width: 16px; }
    .profilePage .srow .left .fa-desktop {
      font-size: 14px; }
  .profilePage .srow .right {
    display: flex;
    color: #666;
    align-items: center; }
    .profilePage .srow .right .pointer {
      margin-left: 10px;
      color: #999;
      font-size: 16px; }

@media only screen and (max-width: 992px) {
  .profilePage .pl-0 {
    padding: 0; } }

@media only screen and (max-width: 992px) {
  .profilePage .mt2 {
    margin-top: 20px;
    margin-bottom: 20px; } }

.text-ref {
  margin-top: 30px !important;
  font-size: 16px;
  background: #ddd;
  max-width: 300px;
  font-weight: bold;
  padding: 20px;
  margin: 0 auto;
  border-radius: 6px;
  text-align: center; }
  .text-ref .points {
    font-size: 28px; }
